export default {
  layout: 'default',
  middleware: 'auth',
  asyncData({ store }) {
    const items = store.loggedInUser.liked_products
    return items
  },
  data() {
    return {
      edit: null,
      search: '',
      allCheck: false,
      page: 1,
      max: 100,
      isLoading: false,
    }
  },
  methods: {
    expandImage(url) {
      this.$swal({
        showCloseButton: true,
        imageUrl: url,
        // width: 100%,
        showConfirmButton: false,
        imageWidth: '100%',
        imageAlt: 'Image Not Available',
      })
    },
    goTo(id) {
      this.$router.push(`/products/${id}`)
    },
    unlike(id) {
      this.isLoading = true
      try {
        console.log('test')
        this.$axios.$delete(`/likes/${id}`)
      } catch (e) {
        console.log(e)
      } finally {
        this.isLoading = false
      }
    },
  },
}
